@media screen and (min-width:1024px){
    .nav{
        padding-top: 100px;
        position: fixed;
        width: 100px;
        height: 100%;
        box-shadow: 0 0 20px 2px white;
        
        animation: reverseshadow 0.5s forwards;
    }
}

@media screen and (max-width:1023px){
    .nav{
        background-color: white;
        display: flex;
        flex-direction: row;
        position: fixed;
        top: calc(100vh - 100px);
        width: 100vw;
        height: 100px;
        box-shadow: 0 0 20px 2px #7e7e7e;
        z-index: 20;
    }
}

.nav:hover{
    animation: shadow 0.5s forwards;
}

@media screen and (min-width:1024px){
    .link{
        cursor: pointer;
        display: grid;
        margin: auto;
        border-radius: 15px;
        width: 66px;
        height: 66px;
        transition: color 0.4 cubic-bezier(0, 0, 0.2, 1);
        margin-bottom: 10px;
        text-decoration: none;
        animation: reverseshadow 0.5s forwards cubic-bezier(0, 0, 0.2, 1);
    }
}

@media screen and (max-width:1023px){
    .link{
        cursor: pointer;
        display: grid;
        margin: auto;
        border-radius: 15px;
        width: 66px;
        height: 66px;
        transition: color 0.4 cubic-bezier(0, 0, 0.2, 1);
        text-decoration: none;
        box-shadow: 0 0 10px 2px #7e7e7e;
    }
}

.link:hover{
    animation: shadow 0.5s forwards cubic-bezier(0, 0, 0.2, 1);
}
i{
    font-size: 30px;
}
i,p{
    margin: auto;
    text-align: center;
    color: black;
}

@keyframes shadow{
    from{
        box-shadow: 0 0 10px 2px white;
    }
    to{
        box-shadow: 0 0 10px 2px #7e7e7e;
    }
}

@keyframes reverseshadow{
    from{
        box-shadow: 0 0 10px 2px #7e7e7e;
    }
    to{
        box-shadow: 0 0 10px 2px white;
    }
}