@import url(https://fonts.googleapis.com/css2?family=Nanum+Gothic&display=swap);
html, body{
 	width: 100%;
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
	font-family: 'Nanum Gothic', sans-serif;
}

article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

@media screen and (min-width:1024px){
    .nav{
        padding-top: 100px;
        position: fixed;
        width: 100px;
        height: 100%;
        box-shadow: 0 0 20px 2px white;
        
        -webkit-animation: reverseshadow 0.5s forwards;
        
                animation: reverseshadow 0.5s forwards;
    }
}

@media screen and (max-width:1023px){
    .nav{
        background-color: white;
        display: flex;
        flex-direction: row;
        position: fixed;
        top: calc(100vh - 100px);
        width: 100vw;
        height: 100px;
        box-shadow: 0 0 20px 2px #7e7e7e;
        z-index: 20;
    }
}

.nav:hover{
    -webkit-animation: shadow 0.5s forwards;
            animation: shadow 0.5s forwards;
}

@media screen and (min-width:1024px){
    .link{
        cursor: pointer;
        display: grid;
        margin: auto;
        border-radius: 15px;
        width: 66px;
        height: 66px;
        transition: color 0.4 cubic-bezier(0, 0, 0.2, 1);
        margin-bottom: 10px;
        text-decoration: none;
        -webkit-animation: reverseshadow 0.5s forwards cubic-bezier(0, 0, 0.2, 1);
                animation: reverseshadow 0.5s forwards cubic-bezier(0, 0, 0.2, 1);
    }
}

@media screen and (max-width:1023px){
    .link{
        cursor: pointer;
        display: grid;
        margin: auto;
        border-radius: 15px;
        width: 66px;
        height: 66px;
        transition: color 0.4 cubic-bezier(0, 0, 0.2, 1);
        text-decoration: none;
        box-shadow: 0 0 10px 2px #7e7e7e;
    }
}

.link:hover{
    -webkit-animation: shadow 0.5s forwards cubic-bezier(0, 0, 0.2, 1);
            animation: shadow 0.5s forwards cubic-bezier(0, 0, 0.2, 1);
}
i{
    font-size: 30px;
}
i,p{
    margin: auto;
    text-align: center;
    color: black;
}

@-webkit-keyframes shadow{
    from{
        box-shadow: 0 0 10px 2px white;
    }
    to{
        box-shadow: 0 0 10px 2px #7e7e7e;
    }
}

@keyframes shadow{
    from{
        box-shadow: 0 0 10px 2px white;
    }
    to{
        box-shadow: 0 0 10px 2px #7e7e7e;
    }
}

@-webkit-keyframes reverseshadow{
    from{
        box-shadow: 0 0 10px 2px #7e7e7e;
    }
    to{
        box-shadow: 0 0 10px 2px white;
    }
}

@keyframes reverseshadow{
    from{
        box-shadow: 0 0 10px 2px #7e7e7e;
    }
    to{
        box-shadow: 0 0 10px 2px white;
    }
}
.Screen{
    position: absolute;
    background-color: skyblue;
    -webkit-animation: fadeout 1s 0.5s forwards;
            animation: fadeout 1s 0.5s forwards;
    width: 100%;
    height: 100vh;
    z-index: 1;
    opacity: 1;
}

@-webkit-keyframes fadeout {
    from {
        opacity: 1;
    }
    to {
        width: 0;
    }
}

@keyframes fadeout {
    from {
        opacity: 1;
    }
    to {
        width: 0;
    }
}

.CoinTrade{
    width: 100%;
}

@media screen and (min-width: 1024px){
    .contents{
        position: absolute;
        height: calc(95% - 62x);
