.OrderBook{
    width: 95%;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: repeat(16,1fr);
}

.OrderBook > div{
    border: 1px solid #aaa;
}

.OrderBook .OrderPriceBlockDiscription{
    text-align: center;
    display: grid;
    grid-template-columns: 2fr 1fr 2fr;
}

.OrderBook .OrderPriceBlock{
    text-align: center;
    display: grid;
    grid-template-columns: 2fr 1fr 2fr;
}