@media screen and (min-width:1024px){
    #cd{
        width: calc(95% - 20px);
        height: 200px;
        border-radius: 30px 30px;
        font-size: 20px;
        padding: 10px;
    }
}
@media screen and (max-width:1023px){
    #cd{
        width: calc(95% - 20px);
        height: 100px;
        border-radius: 30px 30px;
        font-size: 20px;
        padding: 10px;
        margin-bottom: 10px;
    }
}

.CoinDetailRISE{
    background: linear-gradient(to bottom right, #ffd1d1 0%, #ff6161 100%);
    /* background: linear-gradient(0deg, rgba(255,179,179,0.41780462184873945) 0%, rgba(255,160,160,1) 47%, rgba(255,107,107,1) 100%); */
}
.CoinDetailEVEN{
    background: linear-gradient(to bottom right, #b5b5b5 0%, #6b6b6b 100%);
}
.CoinDetailFALL{
    background-color: #045de9;
    background-image: linear-gradient(315deg, #045de9 0%, #09c6f9 74%);
    /* background: linear-gradient(0deg, rgba(179,183,255,0.41780462184873945) 0%, rgba(166,160,255,1) 47%, rgba(115,107,255,1) 100%); */
}


.CoinDetail .content{
    display: flex;
}

@media screen and (min-width:1024px){
    .CoinDetail .content .mainContent{
        width: 400px;
        margin-right: 300px;
        display: grid;
        grid-template-columns:repeat(4,1fr);
        grid-template-rows: repeat(4,1fr);
    }
}

@media screen and (max-width:1023px){
    .CoinDetail .content .mainContent{
        width: 100%;
        display: grid;
        grid-template-columns:repeat(4,1fr);
        grid-template-rows: repeat(4,1fr);
    }
}

.CoinDetail .coinImg{
    grid-row: 1/3;
    height: 20px;
    width: 20px;
}

.CoinDetail .koreanName{
    grid-column: 2/4;
    grid-row: 1/3;
}

.CoinDetail .tradePrice{
    grid-column: 1/5;
    text-align: center;
}

.CoinDetail .changePrice{
    grid-column: 1/3;
    text-align: left;
}

.CoinDetail .changePercent{
    grid-column: 3/5;
    text-align: right;
}

@media screen and (max-width:1023px){
    .CoinDetail .content .detailContent{
        display: none;
        grid-template-columns: repeat(4,1fr);
        grid-template-rows: repeat(4,1fr);
    }
}

@media screen and (min-width:1024px){
    .CoinDetail .content .detailContent{
        display: grid;
        grid-template-columns: repeat(4,1fr);
        grid-template-rows: repeat(4,1fr);
    }
}