@media screen and (min-width: 1024px) {
    .Home{
        display: grid;
        grid-template-columns: repeat(3,1fr);
        grid-template-rows: repeat(3,auto);
    }

    .Home .Main{
        grid-column: 1 / 4;
    }
}

@media screen and (max-width: 1023px) {
    .Home{
        display: grid;
        grid-template-columns: repeat(3,1fr);
        grid-template-rows: repeat(3,auto);
    }

    .Home .Main{
        grid-column: 1 / 4;
    }

    .ChartContainer{
        height: 100%;
    }
}